.component {
  padding-bottom: 2rem;
  background-color: #bbb;
  margin-bottom: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  border-radius: 0.2rem;
  -webkit-box-shadow: inset 0 0 0.4rem #999;
          box-shadow: inset 0 0 0.4rem #999;
}

.progress {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 0.2rem;
  z-index: 2;
  -webkit-transition: width ease-in-out 200ms;
  -o-transition: width ease-in-out 200ms;
  transition: width ease-in-out 200ms;
  background-size: 4rem 2rem, 100% 100%, 100% 100%;
  background-image:
	   -webkit-linear-gradient(-45deg,
	                           transparent 33%, rgba(0, 0, 0, .1) 33%,
	                           rgba(0,0, 0, .1) 66%, transparent 66%),
	   -webkit-linear-gradient(top,
	                           rgba(255, 255, 255, .25),
	                           rgba(0, 0, 0, .25)),
	   -webkit-linear-gradient(left, #67B26F, #4ca2cd);
}

.overOneHundred {
  background-image:
	   -webkit-linear-gradient(-45deg,
	                           transparent 33%, rgba(0, 0, 0, .1) 33%,
	                           rgba(0,0, 0, .1) 66%, transparent 66%),
	   -webkit-linear-gradient(top,
	                           rgba(255, 255, 255, .25),
	                           rgba(0, 0, 0, .25)),
	   -webkit-linear-gradient(left, #ee0979, #ff6a00);
}

.label {
  color: #fff;
  letter-spacing: 0.1rem;
  line-height: 2rem;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
  font-size: 1rem;
  font-family: sans-serif;
}



